import { Link, useLocation } from 'react-router-dom';
import {useState} from 'react';
import './footer.css';
import Navigationcode from './navigationcode';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faAnglesUp } from '@fortawesome/free-solid-svg-icons';
import {faArrowUpWideShort } from '@fortawesome/free-solid-svg-icons';
import {faArrowDownWideShort } from '@fortawesome/free-solid-svg-icons';
import {faHome } from '@fortawesome/free-solid-svg-icons';
import {faFilePen } from '@fortawesome/free-solid-svg-icons';
import {faListCheck } from '@fortawesome/free-solid-svg-icons';
import {faPaste } from '@fortawesome/free-solid-svg-icons';

import logo1 from '../img/logo1.jpeg';


function Footer(){
	// all navigations
	const { goCreate, goHome, goSteps, goTemplates, goResumeForm, scrollToTop, scrollToBottom } = Navigationcode();

	// nav active------------------------------------------------------------------
	  const location = useLocation();
	  const [activeNav, setActiveNav] = useState(location.pathname);

	  const handleNavClick = (path) => {
	    setActiveNav(path);
	  };

	return(
		<>

		<div className="top-down-btn-box">
			<button onClick={scrollToTop}  title="Scroll to Top" className="top-down-btn-item top-down-btn-icon"><FontAwesomeIcon  icon={faArrowUpWideShort} /></button>
			<button onClick={()=>handleNavClick(goHome)} className={activeNav === "/" ? "top-down-btn-item side-nav-active" : "top-down-btn-item"} title="Go to Home"><FontAwesomeIcon  icon={faHome} /></button>
			<button onClick={()=>handleNavClick(goCreate)} className={activeNav === "/create" ? "top-down-btn-item side-nav-active" : "top-down-btn-item"} title="Go to Create"><FontAwesomeIcon  icon={faFilePen} /></button>
			<button onClick={goSteps} className="top-down-btn-item" title="Go to Steps"><FontAwesomeIcon  icon={faListCheck} /></button>
			<button onClick={goTemplates} className="top-down-btn-item " title="Go to Templates"><FontAwesomeIcon  icon={faPaste} /></button>
			<button onClick={scrollToBottom} title="Scroll to Bottom" className="top-down-btn-item top-down-btn-icon"><FontAwesomeIcon  icon={faArrowDownWideShort} /></button></div>
		
		<div className="Container-fluid Container-fluid-no-overflow">
		<div className="row justify-content-center align-items-center">
			<div   className=" col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12  ">
			     <div className="row justify-content-center align-items-center footer-box">
			     	<div   className=" col-6 col-sm-6 col-md-3 col-lg-3 col-xl-3 ">
					     <p className="quote-text">&ldquo; Your resume is your first impression. Make it count. &rdquo;</p>
					</div>
					<div   className=" col-6 col-sm-6 col-md-3 col-lg-3 col-xl-3 ">
						<ul className="footer-list-box">
							<li onClick={goSteps}>3 Easy Steps</li>
							<li onClick={goTemplates}>Templates</li>
							<li onClick={goResumeForm} >Fill Resume Form</li>
							<li onClick={goHome} >Home</li>
							<button onClick={scrollToTop} className="top-btn">Tap to Go on Top <FontAwesomeIcon icon={faAnglesUp} /></button>
						</ul>
					</div>
					<div   className="mt-4 col-6 col-sm-6 col-md-3 col-lg-3 col-xl-3 ">
					    <button onClick={goCreate} className="btn footer-create-btn">Create Resume</button>  
					</div>
					<div   className="mt-4 col-6 col-sm-6 col-md-3 col-lg-3 col-xl-3 ">
					    {/*  <button onClick={goHome} className=" footer-logo">RESUMY.</button> */}
						<div onClick={goHome} className="logo-box">
							<img src={logo1} alt="logo-image-footer" loading="lazy"/>
						</div>
					</div>
			     </div>
			</div>
			<div   className=" col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 copyright-div">
			     &copy; 2024 - RESUMY. by Mukesh Kumar - All rights Reserved
			</div>
		</div>
		</div>
		</>
		);
}

export default Footer